// module/search.ts

// ----------------------  ----------------------

export const CALL_API = 'counter/CALL_API';
//  2 

// ----------------------  ----------------------

export const callapi = (value) => {
    return {
      type: CALL_API,
      payload: value,
    };
  };


// ----------------------  ----------------------

const initialState = {
    movieInfo: [],
};

// ----------------------- ------------------------

function searchReducer(state = initialState, action) {
  switch (action.type) {
    case CALL_API:
      return {
        ...state,
        movieInfo: action.payload
      };
    default:
      return state;
  }
}

export default searchReducer;
